/* 
   FleetGuard Professional Styles
   Theme: Modern Enterprise SaaS (Deep Blue, Clean White, Data-Centric)
*/

:root {
    /* Core Palette */
    --color-primary: #0f172a;      /* Slate 900 */
    --color-secondary: #1e293b;    /* Slate 800 */
    --color-accent: #2563eb;       /* Royal Blue */
    --color-accent-hover: #1d4ed8;
    --color-success: #10b981;
    --color-warning: #f59e0b;
    --color-danger: #ef4444;
    
    /* Backgrounds */
    --bg-body: #f8fafc;            /* Slate 50 */
    --bg-surface: #ffffff;
    --bg-surface-alt: #f1f5f9;     /* Slate 100 */
    
    /* Typography */
    --text-main: #334155;          /* Slate 700 */
    --text-heading: #0f172a;       /* Slate 900 */
    --text-light: #94a3b8;         /* Slate 400 */
    --text-white: #ffffff;

    /* Spacing & Layout */
    --container-width: 1280px;
    --header-height: 80px;
    --radius-sm: 6px;
    --radius-md: 12px;
    --radius-lg: 24px;
    
    /* Effects */
    --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
    --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
    --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
    --shadow-xl: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);
    --glass-bg: rgba(255, 255, 255, 0.7);
    --glass-border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Reset & Base */
* { margin: 0; padding: 0; box-sizing: border-box; }

html { scroll-behavior: smooth; }

body {
    font-family: 'Inter', system-ui, -apple-system, sans-serif;
    background-color: var(--bg-body);
    color: var(--text-main);
    line-height: 1.6;
    -webkit-font-smoothing: antialiased;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Montserrat', sans-serif;
    color: var(--text-heading);
    font-weight: 700;
    line-height: 1.2;
}

a { text-decoration: none; color: inherit; transition: all 0.2s ease; }
ul { list-style: none; }
img { max-width: 100%; display: block; }

/* Utilities */
.container {
    max-width: var(--container-width);
    margin: 0 auto;
    padding: 0 2rem;
}

.text-center { text-align: center; }
.text-gradient {
    background: linear-gradient(135deg, var(--color-accent), #60a5fa);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0.875rem 1.75rem;
    font-weight: 600;
    border-radius: var(--radius-sm);
    transition: all 0.2s ease;
    font-size: 1rem;
    gap: 0.5rem;
    cursor: pointer;
}

.btn-primary {
    background-color: var(--color-accent);
    color: var(--text-white);
    box-shadow: 0 4px 12px rgba(37, 99, 235, 0.3);
}

.btn-primary:hover {
    background-color: var(--color-accent-hover);
    transform: translateY(-1px);
    box-shadow: 0 6px 16px rgba(37, 99, 235, 0.4);
}

.btn-secondary {
    background-color: var(--bg-surface);
    color: var(--text-heading);
    border: 1px solid #e2e8f0;
}

.btn-secondary:hover {
    background-color: #f8fafc;
    border-color: #cbd5e1;
}

.btn-link {
    color: var(--color-accent);
    padding: 0;
    background: none;
}

.btn-link:hover { text-decoration: underline; }

.badge {
    display: inline-block;
    padding: 0.25rem 0.75rem;
    border-radius: 9999px;
    font-size: 0.75rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.badge-blue { background: #dbeafe; color: #1e40af; }
.badge-green { background: #d1fae5; color: #065f46; }

/* Header */
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: var(--header-height);
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(12px);
    border-bottom: 1px solid rgba(0,0,0,0.05);
    z-index: 1000;
}

.nav-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.logo {
    font-size: 1.5rem;
    font-weight: 800;
    color: var(--color-primary);
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.logo i { color: var(--color-accent); }

.nav-links {
    display: flex;
    gap: 2rem;
}

.nav-links a {
    font-weight: 500;
    color: var(--text-main);
    font-size: 0.95rem;
}

.nav-links a:hover { color: var(--color-accent); }

/* Hero Section */
.hero {
    padding-top: calc(var(--header-height) + 4rem);
    padding-bottom: 6rem;
    background: radial-gradient(circle at top right, #f1f5f9 0%, transparent 40%),
                radial-gradient(circle at bottom left, #eff6ff 0%, transparent 40%);
    overflow: hidden;
}

.hero-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    align-items: center;
}

.hero-content h1 {
    font-size: 3.5rem;
    line-height: 1.1;
    margin-bottom: 1.5rem;
    letter-spacing: -0.02em;
}

.hero-content p {
    font-size: 1.25rem;
    color: var(--text-main);
    margin-bottom: 2.5rem;
    max-width: 540px;
}

.hero-actions {
    display: flex;
    gap: 1rem;
    margin-bottom: 3rem;
}

.trust-badges {
    display: flex;
    gap: 2rem;
    align-items: center;
    color: var(--text-light);
    font-size: 0.875rem;
    font-weight: 500;
}

.trust-badges i { margin-right: 0.5rem; color: var(--text-main); }

/* Hero Visual (CSS Dashboard) */
.hero-visual {
    position: relative;
}

.dashboard-card {
    background: var(--bg-surface);
    border-radius: var(--radius-lg);
    box-shadow: var(--shadow-xl);
    border: 1px solid rgba(0,0,0,0.05);
    padding: 1.5rem;
    position: relative;
    z-index: 10;
    transform: perspective(1000px) rotateY(-5deg) rotateX(2deg);
    transition: transform 0.5s ease;
}

.dashboard-card:hover {
    transform: perspective(1000px) rotateY(0deg) rotateX(0deg);
}

.dash-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1.5rem;
    border-bottom: 1px solid #f1f5f9;
    padding-bottom: 1rem;
}

.dash-stat-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    margin-bottom: 1.5rem;
}

.stat-box {
    background: #f8fafc;
    padding: 1rem;
    border-radius: var(--radius-md);
}

.stat-box .label { font-size: 0.75rem; color: var(--text-light); text-transform: uppercase; }
.stat-box .value { font-size: 1.5rem; font-weight: 700; color: var(--text-heading); }
.stat-box .trend { font-size: 0.875rem; display: flex; align-items: center; gap: 0.25rem; margin-top: 0.25rem; }
.trend.up { color: var(--color-success); }
.trend.down { color: var(--color-danger); }

.graph-placeholder {
    height: 120px;
    background: linear-gradient(180deg, rgba(37,99,235,0.1) 0%, rgba(37,99,235,0) 100%);
    border-bottom: 2px solid var(--color-accent);
    position: relative;
    border-radius: 4px;
    overflow: hidden;
}

.graph-line {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="none"><path d="M0 100 L20 60 L40 80 L60 40 L80 50 L100 20" fill="none" stroke="%232563eb" stroke-width="2"/></svg>');
    background-size: 100% 100%;
}

/* Floating Elements */
.float-card {
    position: absolute;
    background: white;
    padding: 1rem;
    border-radius: var(--radius-md);
    box-shadow: var(--shadow-lg);
    display: flex;
    align-items: center;
    gap: 1rem;
    z-index: 20;
    animation: float 6s ease-in-out infinite;
}

.float-card.alert {
    top: -20px;
    right: -20px;
    border-left: 4px solid var(--color-warning);
}

.float-card.success {
    bottom: -20px;
    left: -20px;
    border-left: 4px solid var(--color-success);
}

@keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
}

/* Features Section */
.features {
    padding: 6rem 0;
    background: var(--bg-surface);
}

.section-title {
    text-align: center;
    max-width: 700px;
    margin: 0 auto 4rem;
}

.section-title h2 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
}

.section-title p {
    color: var(--text-light);
    font-size: 1.125rem;
}

.feature-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
}

.feature-card {
    padding: 2rem;
    border-radius: var(--radius-md);
    background: var(--bg-surface);
    border: 1px solid #e2e8f0;
    transition: all 0.3s ease;
}

.feature-card:hover {
    border-color: var(--color-accent);
    box-shadow: var(--shadow-lg);
    transform: translateY(-5px);
}

.icon-box {
    width: 56px;
    height: 56px;
    background: #eff6ff;
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color-accent);
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
}

.feature-card h3 {
    font-size: 1.25rem;
    margin-bottom: 0.75rem;
}

.feature-card p {
    color: var(--text-main);
    font-size: 0.95rem;
}

/* Stats Section */
.stats-section {
    background: var(--color-primary);
    color: white;
    padding: 4rem 0;
}

.stats-container {
    display: flex;
    justify-content: space-around;
    text-align: center;
}

.stat-item h3 {
    color: white;
    font-size: 3rem;
    margin-bottom: 0.5rem;
}

.stat-item p {
    color: #94a3b8;
    font-size: 0.875rem;
    text-transform: uppercase;
    letter-spacing: 1px;
}

/* CTA Section */
.cta-section {
    padding: 6rem 0;
    text-align: center;
    background: linear-gradient(135deg, #eff6ff 0%, #ffffff 100%);
}

.cta-box {
    max-width: 800px;
    margin: 0 auto;
}

.cta-box h2 {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
}

/* Footer */
.footer {
    background: var(--bg-surface);
    border-top: 1px solid #e2e8f0;
    padding: 4rem 0 2rem;
}

.footer-grid {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr;
    gap: 4rem;
    margin-bottom: 4rem;
}

.footer-brand p {
    color: var(--text-light);
    margin-top: 1rem;
    max-width: 300px;
}

.footer-col h4 {
    font-size: 0.875rem;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    margin-bottom: 1.5rem;
    color: var(--text-heading);
}

.footer-col ul li { margin-bottom: 0.75rem; }
.footer-col ul li a { color: var(--text-main); font-size: 0.9rem; }
.footer-col ul li a:hover { color: var(--color-accent); }

.footer-bottom {
    border-top: 1px solid #e2e8f0;
    padding-top: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--text-light);
    font-size: 0.875rem;
}

/* --- Authentication & Dashboard Styles --- */

/* Login Page */
.auth-container {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--bg-body);
    padding: 1rem;
}

.auth-card {
    background: var(--bg-surface);
    width: 100%;
    max-width: 400px;
    padding: 2.5rem;
    border-radius: var(--radius-md);
    border: 1px solid #e2e8f0;
    box-shadow: var(--shadow-lg);
    text-align: center;
}

.auth-header {
    margin-bottom: 2rem;
}

.auth-header h2 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.auth-header p {
    color: var(--text-light);
    font-size: 0.9rem;
}

.form-group {
    margin-bottom: 1.5rem;
    text-align: left;
}

.form-group label {
    display: block;
    font-size: 0.875rem;
    font-weight: 500;
    margin-bottom: 0.5rem;
    color: var(--text-main);
}

.form-input {
    width: 100%;
    padding: 0.75rem 0;
    border: none;
    border-bottom: 1px solid #e2e8f0;
    background: transparent;
    font-family: inherit;
    font-size: 1rem;
    transition: border-color 0.2s;
    border-radius: 0;
}

.form-input:focus {
    outline: none;
    border-bottom-color: var(--color-accent);
}

.auth-divider {
    display: flex;
    align-items: center;
    margin: 1.5rem 0;
    color: var(--text-light);
    font-size: 0.875rem;
}

.auth-divider::before,
.auth-divider::after {
    content: "";
    flex: 1;
    height: 1px;
    background: #e2e8f0;
}

.auth-divider span {
    padding: 0 1rem;
}

.btn-google {
    width: 100%;
    background: white;
    border: 1px solid #e2e8f0;
    color: var(--text-heading);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.75rem;
}

.btn-google:hover {
    background: #f8fafc;
    border-color: #cbd5e1;
}

.btn-full {
    width: 100%;
}

.error-message {
    color: var(--color-danger);
    font-size: 0.8rem;
    margin-top: 0.25rem;
    display: none;
}

/* Dashboard Layout */
.dashboard-body {
    background-color: var(--bg-body);
    min-height: 100vh;
}

.dashboard-nav {
    background: white;
    border-bottom: 1px solid #e2e8f0;
    padding: 0 2rem;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 100;
}

.user-menu {
    display: flex;
    align-items: center;
    gap: 1.5rem;
}

.user-info {
    text-align: right;
    font-size: 0.9rem;
}

.user-name {
    font-weight: 600;
    color: var(--text-heading);
    display: block;
}

.user-email {
    color: var(--text-light);
    font-size: 0.8rem;
}

.dashboard-container {
    max-width: 1200px;
    margin: 2rem auto;
    padding: 0 2rem;
}

.dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
}

/* Vehicle Grid */
.vehicle-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 1.5rem;
}

.vehicle-card {
    background: white;
    border: 1px solid #e2e8f0;
    border-radius: var(--radius-md);
    padding: 1.5rem;
    position: relative;
    transition: all 0.2s ease;
}

.vehicle-card:hover {
    border-color: var(--color-accent);
    box-shadow: var(--shadow-md);
}

.vehicle-card-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 1rem;
}

.vehicle-name {
    font-size: 1.125rem;
    font-weight: 700;
    color: var(--text-heading);
    margin-bottom: 0.25rem;
}

.vehicle-plate {
    font-family: monospace;
    color: var(--text-light);
    background: #f1f5f9;
    padding: 0.25rem 0.5rem;
    border-radius: 4px;
    font-size: 0.875rem;
}

.btn-delete {
    background: none;
    border: none;
    color: var(--text-light);
    cursor: pointer;
    padding: 0.25rem;
    transition: color 0.2s;
}

.btn-delete:hover {
    color: var(--color-danger);
}

.empty-state {
    text-align: center;
    padding: 4rem 2rem;
    background: white;
    border-radius: var(--radius-md);
    border: 1px dashed #e2e8f0;
}

.empty-icon {
    font-size: 3rem;
    color: #cbd5e1;
    margin-bottom: 1rem;
}

/* Floating Action Button */
.fab-add {
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background: var(--color-accent);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    box-shadow: var(--shadow-lg);
    cursor: pointer;
    transition: transform 0.2s;
    border: none;
    z-index: 90;
}

.fab-add:hover {
    transform: scale(1.1);
    background: var(--color-accent-hover);
}

/* Modal */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    transition: all 0.2s;
}

.modal-overlay.active {
    opacity: 1;
    visibility: visible;
}

.modal-card {
    background: white;
    width: 100%;
    max-width: 500px;
    border-radius: var(--radius-md);
    padding: 2rem;
    position: relative;
    transform: translateY(20px);
    transition: transform 0.2s;
}

.modal-overlay.active .modal-card {
    transform: translateY(0);
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
}

.modal-title {
    font-size: 1.5rem;
    font-weight: 700;
}

.btn-close {
    background: none;
    border: none;
    font-size: 1.25rem;
    cursor: pointer;
    color: var(--text-light);
}

.modal-actions {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
    margin-top: 2rem;
}

/* Toast Notification */
.toast {
    position: fixed;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%) translateY(100px);
    background: var(--color-primary);
    color: white;
    padding: 0.75rem 1.5rem;
    border-radius: var(--radius-sm);
    box-shadow: var(--shadow-lg);
    transition: transform 0.3s ease;
    z-index: 2000;
    display: flex;
    align-items: center;
    gap: 0.75rem;
}

.toast.show {
    transform: translateX(-50%) translateY(0);
}

.toast.success i { color: var(--color-success); }
.toast.error i { color: var(--color-danger); }

/* =========================================
   Analysis Page Styles
   ========================================= */

.analysis-body {
    background-color: var(--bg-body);
    min-height: 100vh;
    padding: 2rem;
}

.analysis-container {
    max-width: var(--container-width);
    margin: 0 auto;
}

.analysis-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    background: var(--bg-surface);
    padding: 1.5rem 2rem;
    border-radius: var(--radius-md);
    box-shadow: var(--shadow-sm);
}

.header-info h1 {
    font-size: 1.8rem;
    color: var(--text-heading);
    margin-bottom: 0.5rem;
}

.vehicle-card-mini {
    display: flex;
    gap: 1.5rem;
    color: var(--text-light);
    font-size: 0.9rem;
}

.vehicle-card-mini span i {
    margin-right: 0.5rem;
    color: var(--color-accent);
}

.health-score-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.health-circle {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: conic-gradient(var(--color-success) 82%, #e2e8f0 0);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.health-circle::before {
    content: '';
    position: absolute;
    width: 65px;
    height: 65px;
    background: var(--bg-surface);
    border-radius: 50%;
}

.health-circle span {
    position: relative;
    font-weight: 700;
    font-size: 1.2rem;
    color: var(--text-heading);
}

.ai-insight-box {
    background: #eef2ff;
    border-left: 4px solid var(--color-accent);
    padding: 1.5rem;
    border-radius: var(--radius-sm);
    margin-bottom: 2rem;
    box-shadow: var(--shadow-sm);
}

.ai-insight-box h3 {
    color: var(--color-accent);
    font-size: 1.1rem;
    margin-bottom: 0.5rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.analysis-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
}

.analysis-col {
    background: var(--bg-surface);
    padding: 1.5rem;
    border-radius: var(--radius-md);
    box-shadow: var(--shadow-md);
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.col-header {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid var(--bg-surface-alt);
}

.col-header h2 {
    font-size: 1.2rem;
    font-weight: 600;
}

.col-header i {
    font-size: 1.2rem;
}

.issue-card {
    background: var(--bg-surface-alt);
    padding: 1rem;
    border-radius: var(--radius-sm);
    border: 1px solid transparent;
    transition: transform 0.2s, box-shadow 0.2s;
}

.issue-card:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-sm);
}

.issue-card.good {
    background: #f0fdf4; /* Light green bg */
    border-color: #bbf7d0;
}

.issue-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.75rem;
}

.issue-title {
    font-weight: 600;
    color: var(--text-heading);
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.badge-urgent, .badge-medium, .badge-good {
    font-size: 0.7rem;
    padding: 0.2rem 0.5rem;
    border-radius: 99px;
    font-weight: 700;
    text-transform: uppercase;
}

.badge-urgent {
    background: #fef2f2;
    color: var(--color-danger);
    border: 1px solid #fecaca;
}

.badge-medium {
    background: #fffbeb;
    color: var(--color-warning);
    border: 1px solid #fde68a;
}

.badge-good {
    background: #dcfce7;
    color: var(--color-success);
    border: 1px solid #86efac;
}

.issue-desc {
    font-size: 0.9rem;
    color: var(--text-main);
    margin-bottom: 1rem;
    line-height: 1.5;
}

.issue-meta {
    display: flex;
    justify-content: space-between;
    font-size: 0.85rem;
    color: var(--text-light);
    margin-bottom: 1rem;
    font-weight: 500;
}

.btn-sm {
    padding: 0.4rem 0.8rem;
    font-size: 0.85rem;
    width: 100%;
}

/* =========================================
   Analysis Page - Accordion & Summary
   ========================================= */

.accordion-section {
    background: var(--bg-surface);
    border-radius: var(--radius-sm);
    margin-bottom: 1rem;
    box-shadow: var(--shadow-sm);
    overflow: hidden;
}

.accordion-header {
    padding: 1rem 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    background: var(--bg-surface);
    transition: background 0.2s;
}

.accordion-header:hover {
    background: var(--bg-surface-alt);
}

.acc-title {
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 0.75rem;
    color: var(--text-heading);
}

.health-bar-mini {
    width: 100px;
    height: 6px;
    background: #e2e8f0;
    border-radius: 99px;
    overflow: hidden;
}

.bar-fill {
    height: 100%;
    background: var(--color-success);
    border-radius: 99px;
}

.bar-fill.warning { background: var(--color-warning); }
.bar-fill.danger { background: var(--color-danger); }

.accordion-content {
    padding: 0 1.5rem;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-out, padding 0.3s ease;
    background: var(--bg-surface-alt);
}

.accordion-content.open {
    padding: 1.5rem;
    max-height: 1000px; /* Arbitrary large height for animation */
    border-top: 1px solid #e2e8f0;
}

.check-list {
    list-style: none;
    margin-bottom: 1rem;
}

.check-item {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    margin-bottom: 0.5rem;
    font-size: 0.95rem;
    color: var(--text-main);
}

.check-item i {
    width: 20px;
    text-align: center;
}

.check-item .fa-check { color: var(--color-success); }
.check-item .fa-triangle-exclamation { color: var(--color-warning); }
.check-item .fa-xmark { color: var(--color-danger); }

.file-preview-grid {
    display: flex;
    gap: 1rem;
}

.file-thumbnail {
    width: 60px;
    height: 60px;
    border-radius: var(--radius-sm);
    overflow: hidden;
    border: 1px solid #e2e8f0;
}

.file-thumbnail img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.cost-summary-card {
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    background: var(--bg-surface);
    padding: 1.5rem;
    border-radius: var(--radius-md);
    box-shadow: var(--shadow-xl);
    width: 300px;
    z-index: 100;
    border: 1px solid #e2e8f0;
}

/* =========================================
   Logistics Premium Theme (Overrides)
   ========================================= */
:root {
    --logistics-black: #000000;
    --logistics-white: #FFFFFF;
    --logistics-yellow: #FFD700;
    --logistics-ocher: #CC8800;
    --logistics-blue: #002366;
}

.theme-logistics {
    --color-primary: var(--logistics-black);
    --color-accent: var(--logistics-yellow);
    --color-accent-hover: #e6c200;
    --text-heading: var(--logistics-black);
}

.theme-logistics .btn-primary {
    background-color: var(--logistics-yellow);
    color: var(--logistics-black);
    box-shadow: 0 4px 15px rgba(255, 215, 0, 0.4);
}

.theme-logistics .btn-primary:hover {
    background-color: var(--color-accent-hover);
    transform: translateY(-2px);
}

.theme-logistics .section-header {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1.5rem;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid var(--logistics-ocher);
}

.theme-logistics .section-header i {
    color: var(--logistics-blue);
    font-size: 1.5rem;
}

.theme-logistics .form-section {
    background: var(--bg-surface);
    padding: 2rem;
    border-radius: var(--radius-md); /* Keeping rounded for consistency, but can be sharper */
    box-shadow: var(--shadow-md);
    margin-bottom: 2rem;
    border-left: 4px solid var(--logistics-blue);
    position: relative;
    overflow: hidden;
}

.theme-logistics .form-section::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100px;
    height: 100px;
    background: linear-gradient(135deg, transparent 50%, rgba(255, 215, 0, 0.1) 50%);
    pointer-events: none;
}

.theme-logistics .input-group label {
    color: var(--logistics-blue);
    font-weight: 600;
}

.theme-logistics .checkbox-group input:checked + label::before {
    background-color: var(--logistics-yellow);
    border-color: var(--logistics-yellow);
}

.theme-logistics .status-badge {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.25rem 0.75rem;
    border-radius: 4px;
    font-size: 0.85rem;
    font-weight: 700;
    background: var(--logistics-blue);
    color: var(--logistics-white);
}

.theme-logistics .status-badge.warning {
    background: var(--logistics-ocher);
}

/* Asymmetric Card Style */
.asymmetric-card {
    clip-path: polygon(0 0, 100% 0, 100% 95%, 95% 100%, 0 100%);
}

/* Floating Label Input Style Override */
.theme-logistics .input-group input:focus {
    border-bottom-color: var(--logistics-yellow);
}

.theme-logistics .input-group input:focus ~ label {
    color: var(--logistics-ocher);
}

/* =========================================
   Vehicle Inspection Page Layout & Components
   ========================================= */

.vehicle-inspection-page {
    min-height: 100vh;
    background-color: #f8fafc;
    padding: 2rem;
}

.main-container {
    display: flex;
    gap: 2rem;
    max-width: 1400px;
    margin: 0 auto;
    align-items: flex-start;
}

/* Left Panel */
.left-panel {
    flex: 1;
    position: sticky;
    top: 2rem;
    background: var(--logistics-black);
    color: white;
    border-radius: var(--radius-lg);
    padding: 2rem;
    min-height: 600px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    box-shadow: var(--shadow-xl);
}

.brand-mark {
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 1.5rem;
    font-weight: 800;
    margin-bottom: 3rem;
    z-index: 10;
}

.brand-mark i { color: var(--logistics-yellow); }

.vehicle-preview {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    z-index: 10;
}

.preview-details {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    padding: 2rem;
    border-left: 4px solid var(--logistics-yellow);
    margin-top: auto;
}

.preview-details h3 {
    color: var(--logistics-yellow);
    margin-bottom: 1rem;
    font-size: 1.2rem;
}

.preview-details p {
    margin-bottom: 0.5rem;
    font-size: 1.1rem;
}

/* Right Panel */
.right-panel {
    flex: 2;
}

.form-header {
    margin-bottom: 2rem;
    padding: 2rem;
    background: white;
    border-radius: var(--radius-md);
    box-shadow: var(--shadow-sm);
    border-left: 6px solid var(--logistics-blue);
}

.form-header h1 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
}

/* Form Components */
.toggle-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 0;
    border-bottom: 1px solid #f1f5f9;
}

.toggle-wrapper:last-child { border-bottom: none; }

.toggle-switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 26px;
}

.toggle-switch input { opacity: 0; width: 0; height: 0; }

.slider {
    position: absolute;
    cursor: pointer;
    top: 0; left: 0; right: 0; bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 34px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: var(--logistics-blue);
}

input:checked + .slider:before {
    transform: translateX(24px);
}

/* Checkbox Grid */
.checkbox-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1rem;
    margin-top: 1rem;
}

.checkbox-group {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 0.75rem;
    background: #f8fafc;
    border-radius: var(--radius-sm);
    transition: all 0.2s;
}

.checkbox-group:hover {
    background: #f1f5f9;
}

.checkbox-group input[type="checkbox"] {
    width: 18px;
    height: 18px;
    accent-color: var(--logistics-blue);
}

/* Floating Inputs */
.input-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
}

.input-group {
    position: relative;
    margin-top: 1rem;
}

.input-group.full-width { grid-column: 1 / -1; }

.input-group input, .input-group select {
    width: 100%;
    padding: 0.8rem 0;
    font-size: 1rem;
    border: none;
    border-bottom: 2px solid #e2e8f0;
    outline: none;
    background: transparent;
    transition: border-color 0.2s;
}

.input-group label {
    position: absolute;
    top: 0.8rem;
    left: 0;
    font-size: 1rem;
    color: #94a3b8;
    pointer-events: none;
    transition: 0.2s ease all;
}

.input-group input:focus,
.input-group input:not(:placeholder-shown) {
    border-bottom-color: var(--logistics-blue);
}

.input-group input:focus ~ label,
.input-group input:not(:placeholder-shown) ~ label {
    top: -1.2rem;
    font-size: 0.85rem;
    color: var(--logistics-blue);
    font-weight: 600;
}

/* Dashboard Warning Lights */
.status-indicator-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.75rem 0;
    border-bottom: 1px solid #f1f5f9;
}

.status-btn {
    padding: 0.25rem 1rem;
    border-radius: 20px;
    border: 1px solid #e2e8f0;
    background: white;
    color: #94a3b8;
    font-weight: 700;
    font-size: 0.8rem;
    cursor: pointer;
    transition: all 0.2s;
}

.status-btn.active {
    background: var(--color-danger);
    color: white;
    border-color: var(--color-danger);
    box-shadow: 0 0 10px rgba(239, 68, 68, 0.4);
}

.form-actions {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
    margin-top: 3rem;
    padding-top: 2rem;
    border-top: 1px solid #e2e8f0;
}

/* Car Animation Placeholder (Simplified) */
.animation-container {
    height: 200px;
    position: relative;
    margin-bottom: 2rem;
    overflow: hidden;
    border-radius: var(--radius-md);
    background: linear-gradient(to bottom, #87CEEB 0%, #E0F7FA 100%);
}

.street {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 40px;
    background: #555;
}

.car {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 60px;
    background: var(--logistics-blue);
    border-radius: 10px 10px 0 0;
}

.car::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 10px;
    width: 20px;
    height: 20px;
    background: #333;
    border-radius: 50%;
    box-shadow: 60px 0 0 #333;
}

